.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 0;
  background: linear-gradient(90deg, #050b5c, #1a73e8);
  box-shadow: 0 3px 5px 0 rgb(0 4 56 / 50%);
  z-index: 9;
}

.data {
  display: flex;
  align-items: center;
  margin: 0 auto;
  font-weight: 500;
}

.svg {
  margin-left: 5px;
  color: #000000;
  width: 25px;
  height: 25px;
  transform: scale(1);
  transition: all 250ms;

  &:hover {
    opacity: 0.8;
    transform: scale(1.1);
  }
}
